<template>
  <div>
    <v-row no-gutters>
      <!--         left part **** Starts ****-->
      <v-col>
        <!--        top left part for product list **** Starts ****-->
        <v-card :height="screen_height_for_left_top" class="pa-2 overflow-auto" outlined tile>
          <v-data-table
            :headers="headers"
            :items="desserts"
            :items-per-page="10"
            class="elevation-1"
          >
            <template v-slot:item.serial_no="props">
              <v-edit-dialog
                :return-value.sync="props.item.serial_no"
                large
                persistent
                @save="save"
                @cancel="cancel"
                @open="open"
                @close="close"
              >
                <div>{{ props.item.serial_no }}</div>
                <template v-slot:input>
                  <div class="mt-4 text-h6">
                    Update Name
                  </div>
                  <v-text-field
                    v-model="props.item.serial_no"
                    :rules="[max25chars]"
                    label="Edit"
                    single-line
                    counter
                    autofocus
                  ></v-text-field>
                </template>
              </v-edit-dialog>
            </template>
            <template v-slot:item.actions="{ item }">
              <v-icon
                small
                class="mr-2"
                @click="editItem(item)"
              >
                mdi-pencil
              </v-icon>
              <v-icon
                small
                @click="deleteItem(item)"
              >
                mdi-delete
              </v-icon>
            </template>
          </v-data-table>
          <v-snackbar
            v-model="snack"
            :timeout="2000"
            :color="snackColor"
          >
            {{ snackText }}

            <template v-slot:action="{ attrs }">
              <v-btn
                v-bind="attrs"
                text
                @click="snack = false"
              >
                Close
              </v-btn>
            </template>
          </v-snackbar>
        </v-card>
        <!--        top left part for product list **** Ends ****-->
        <!--        down left part for calculation **** Starts ****-->
        <v-card
          :height="screen_height_for_left_down" class="pa-2 overflow-auto" outlined tile>
          {{ screen_height_for_right_down }}
          <v-btn
            @click="test_alert()">
            press
          </v-btn>
        </v-card>
        <!--        down left part for calculation **** Ends ****-->
      </v-col>
      <!--         left part **** Ends ****-->
      <!--         Right part **** Starts ****-->
      <v-col>
        <v-card height="75" class="pa-2 " outlined tile>

          <v-row>
            <v-col cols="12" sm="6">
              <v-text-field
                prepend-inner-icon="mdi-shopping-search"
                v-model="message4"
                label="Search"
                outlined
                clearable
              ></v-text-field>
            </v-col>
            <v-col cols="12" sm="6">
              <v-text-field
                prepend-inner-icon="mdi-barcode-scan"
                v-model="message4"
                label="Barcode"
                outlined
                clearable
              ></v-text-field>
            </v-col>
          </v-row>

        </v-card>
        <v-card :height="screen_height_for_right_down" class="pa-2 overflow-auto" outlined tile>
          <div>

          </div>
          <v-btn v-for="n in 50" :key="n" class="mx-2 mt-1" dark height="100" width="100" color="primary">
            <v-icon dark>
              mdi-minus
            </v-icon>
          </v-btn>
        </v-card>
      </v-col>
      <!--         Right part **** Ends ****-->
    </v-row>
  </div>

</template>

<script>
import Swal from "sweetalert2";

export default {
  name: "POS",
  data: () => ({
    // screen size value ** starts here **
    screen_height_for_right_down: window.innerHeight - 127,
    screen_height_for_left_top: ((window.innerHeight - 52) / 3) * 2,
    screen_height_for_left_down: ((window.innerHeight - 52) / 3),
    // screen size value ** ends here **
    // Table Management Section ** Starts here **
    snack: false,
    snackColor: "",
    snackText: "",
    max25chars: v => v.length <= 25 || "Input too long!",
    headers: [
      { text: "Serial No", sortable: false, value: "serial_no" },
      { text: "Description", value: "description" },
      { text: "QTY", value: "qty" },
      { text: "Rate", value: "rate" },
      { text: "Amount", value: "amount" },
      { text: "Action", value: "actions", sortable: false }
    ],
    desserts: [
      {
        serial_no: "Frozen Yogurt",
        description: 159,
        qty: 6.0,
        rate: 24,
        amount: 4.0
      }
    ]
    // Table Management Section ** Ends here **
  }),

  computed: {},

  methods: {
    // live table edit code ** starts here **
    save() {
      this.snack = true;
      this.snackColor = "success";
      this.snackText = "Data saved";
    },
    cancel() {
      this.snack = true;
      this.snackColor = "error";
      this.snackText = "Canceled";
    },
    open() {
      this.snack = true;
      this.snackColor = "info";
      this.snackText = "Dialog opened";
    },
    close() {
      console.log("Dialog closed");
    },
    // live table edit code ** ends here **
    test_alert() {
      Swal.fire({
        title: "Are you sure?",
        text: "You won't be able to revert this!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Yes, delete it!"
      }).then((response) => {
        if (response.isConfirmed) {
          this.desserts.push({
            serial_no: "KitKat",
            description: 518,
            qty: 26.0,
            rate: 65,
            amount: 7
          });
          Swal.fire("Deleted!", "Designation has been deleted!!", "success");
        } else {
          Swal.fire("Cancelled", "Designation has not been deleted !", "error");
        }
      });
    }
  },
  created() {
    console.log(this.screen_height_for_right_down);
  }
};
</script>

<style scoped>

</style>